<template>
  <div>
    <div class="container">
      <h1>Fabricante</h1>
    </div>
    <maker-table
        :data="makerListState"
        @DeleteMaker="DeleteMaker"
        @addMaker="addMaker"
        @updateMaker="updateMaker"
    />
  </div>
</template>

<script>

import MakerTable from "@/components/maker/MakerTable";
import {mapActions, mapGetters} from "vuex";
export default {
  name: "Maker",
  components: {
    MakerTable
  },
  data () {
    return {
      jsonDataUpdate: {
          acronym: "",
          address: "",
          businessName: "",
          contact: "",
          country: "",
          id: 0,
          telephone: "",
          timesTammp: ""
      }
    }
  },
  created() {
    this.getMakerState()
  },
  computed: {
    ...mapGetters(['makerListState'])
  },
  methods: {
    ...mapActions(['getMakerState', 'setMakernew', 'apiUpdateMaker', 'apiDeleteMaker']),

    addMaker( item) {
      this.setMakernew(item)
    },
    updateMaker( item) {
      this.jsonDataUpdate.acronym = item.acronym;      
      this.jsonDataUpdate.address = item.address;      
      this.jsonDataUpdate.businessName = item.businessName;
      this.jsonDataUpdate.contact = item.contact;
      this.jsonDataUpdate.country = item.country;      
      this.jsonDataUpdate.id = item.id;      
      this.jsonDataUpdate.telephone = item.telephone;      
      this.jsonDataUpdate.timesTammp = item.timesTammp;
       
     
      this.apiUpdateMaker(this.jsonDataUpdate)
    },
    DeleteMaker(id) {
      this.apiDeleteMaker(id)
    }
  }
}
</script>

